<template>
  <div>
    <van-swipe class="my-swipe"
               indicator-color="white"
               :loop="false"
               @change="onChange">
      <van-swipe-item v-for="(item, i) in imgs"
                      :key="i"
                      @click='change(i)'>
        <img :src="item"
             alt="">
      </van-swipe-item>
      <template #indicator>
        <div class="custom-indicator">
          {{ current + 1 }}/4
        </div>
      </template>

    </van-swipe>
  </div>
</template>
<script>
import { Toast } from 'vant'
export default {
  data () {
    return {
      imgs: [
        require('@/assets/imgs/slide1.png'),
        require('@/assets/imgs/slide2.png'),
        require('@/assets/imgs/slide3.png'),
        require('@/assets/imgs/slide4.png'),
      ],
      current: 0
    }
  },
  methods: {
    onChange (index) {
      this.current = index
      if (index == this.imgs.length - 1) {

      }
      Toast()
    },
    change (i) {
      i == this.imgs.length - 1 ? this.$router.push({ name: "now" }) : null
    }
  },
}
</script>
<style lang="scss" scoped>
img {
  width: 100%;
  height: 100%;
}
.custom-indicator {
  position: absolute;
  right: 20px;
  bottom: 10px;
  color: gray;
  font-size: 20px;
}
</style>